@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
/**
 * YUI 3.5.0 - reset.css (http://developer.yahoo.com/yui/3/cssreset/)
 * http://cssreset.com/
 * Copyright 2012 Yahoo! Inc. All rights reserved.
 * http://yuilibrary.com/license/
 */
/*
	TODO will need to remove settings on HTML since we can't namespace it.
	TODO with the prefix, should I group by selector or property for weight savings?
*/
html{
	color:#000;
	background:#FFF;
}
/*
	TODO remove settings on BODY since we can't namespace it.
*/
/*
	TODO test putting a class on HEAD.
		- Fails on FF.
*/
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td {
	margin:0;
	padding:0;
}
table {
	border-collapse:collapse;
	border-spacing:0;
}
fieldset,
img {
	border:0;
}
/*
	TODO think about hanlding inheritence differently, maybe letting IE6 fail a bit...
*/
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
	font-style:normal;
	font-weight:normal;
}
 
ol,
ul {
	list-style:none;
}
 
caption,
th {
	text-align:left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-size:100%;
	font-weight:normal;
}
q:before,
q:after {
	content:'';
}
abbr,
acronym {
	border:0;
	-webkit-font-feature-settings:normal;
	        font-feature-settings:normal;
	font-variant:normal;
}
/* to preserve line-height and selector appearance */
sup {
	vertical-align:text-top;
}
sub {
	vertical-align:text-bottom;
}
input,
textarea,
select {
	font-family:inherit;
	font-size:inherit;
	font-weight:inherit;
}
/*to enable resizing for IE*/
input,
textarea,
select {
	*font-size:100%;
}
/*because legend doesn't inherit in IE */
legend {
	color:#000;
}
/* YUI CSS Detection Stamp */
#yui3-css-stamp.cssreset { display: none; }
body{margin:0;font-family:'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#EEFBFB}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}h1{font-size:22px}h2{font-size:18px}h3{font-size:17px}p{font-size:15px;padding:1em}h1,h2{text-transform:uppercase}


.intro-question{height:90vh;background-color:#12232E;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;text-align:center}.intro-question h1{letter-spacing:0.3em;margin:1em}.intro{background-color:#4f77a8;text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}.intro h1,.intro h2{letter-spacing:0.3em}.intro .intro__content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;padding:7em 0;max-width:700px;-webkit-align-items:center;align-items:center}.intro .intro__content h2{margin-top:1em}.intro .intro__content img{border-radius:50%;width:200px;margin:1em 0;box-shadow:0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12)}@media screen and (min-width: 820px){.intro-question{height:95vh}.intro__content{padding:4em 0}}

.skills{min-height:100vh;background-color:#12232E;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.skills h2{margin:1em}.skills .skills__window{min-width:300px;max-width:700px;min-height:300px;background-color:#203647;border-radius:8px;margin-bottom:4em;box-shadow:0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12)}.skills .skills__window .window__header{display:-webkit-flex;display:flex;height:35px;width:100%;background-color:#4f77a8;border-top-left-radius:8px;border-top-right-radius:8px}.skills .skills__window .window__header .window_control-panel{display:-webkit-flex;display:flex;-webkit-align-self:center;align-self:center}.skills .skills__window .window__header .window_control-panel div{width:12px;height:12px;display:block;border-radius:50%;margin-left:0.5em}.skills .skills__window .window__header .window_control-panel div:nth-of-type(1){background-color:#FC4647;margin-left:1em}.skills .skills__window .window__header .window_control-panel div:nth-of-type(2){background-color:#FBB025}.skills .skills__window .window__header .window_control-panel div:nth-of-type(3){background-color:#2AC131}.skills .skills__window .window__content{text-align:left;padding:0.5em;line-height:1.5em}.skills .skills__window .window__content span{color:rgba(160,160,160,0.5)}@media screen and (min-width: 820px){.skills__window{width:500px}}

.workslist-container{min-height:100vh;background-color:#4f77a8;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.workslist-container h2{padding-top:4em}.workslist-container .list{width:95%;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center}.workslist-container .list .list__item{background-color:#203647;transition-duration:300ms;margin-top:1.5em;box-shadow:0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12);border-radius:8px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between}.workslist-container .list .list__item h3{font-weight:bold}.workslist-container .list .list__item:last-child{margin-bottom:4em}.workslist-container .list .list__item img{width:100%;border-top-left-radius:8px;border-top-right-radius:8px}.workslist-container .list .list__item .list__item__description h3{padding:1em 0 0 1em}.workslist-container .list .list__item a{text-decoration:none;text-transform:uppercase;margin:1em;padding:0.3em 0.5em;color:#58b4ee;-webkit-align-self:center;align-self:center}.workslist-container .list .list__item a:visited{color:#58b4ee}.workslist-container .list .list__item a:hover{cursor:pointer;background-color:#244a5f}.project-description{background-color:#12232E;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;text-align:center;-webkit-justify-content:flex-start;justify-content:flex-start;position:relative;padding:0 0 1em 0}.project-description :first-child{width:350px}.project-description img{width:350px}.project-description h1{margin:2em 0 0.5em 0}.project-description p{padding:0.5em 0}.project-description a{color:#EEFBFB;text-decoration:none;cursor:pointer;width:inherit}.project-description a:visited{color:inherit}.project-description ul{border:#4DA8DA 1px solid;margin:0.5em 0}.project-description ul li{font-size:0.9em;line-height:1.5em;color:gold}.project-description ul li:last-child{margin-bottom:0.5em}.project-description .project-link{border-bottom:#4DA8DA 1px solid;padding:0.5em;margin-right:0.5em}.project-description .project-link:hover{opacity:0.5}.project-description .home-link{position:absolute;bottom:2em;right:0.5em;padding:0.5em 1em;opacity:0.8;transition-duration:300ms;margin:1em}.project-description .home-link:hover{-webkit-transform:scale(1.1);transform:scale(1.1);opacity:1;background-color:#4DA8DA;cursor:pointer}@media screen and (min-width: 600px){.list__item{width:350px;height:400px;margin-left:1em}.project-description img{width:450px}.project-description :first-child{width:450px}}

.contacts-container{background-color:#12232E;padding:4em 0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.contacts-container>div{width:95%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.contacts-container h2{letter-spacing:0.3em;margin-bottom:2em;text-align:center}.contacts-container a{text-decoration:none;color:#EEFBFB}.contacts-container .contacts-item{background-color:#4f77a8;box-shadow:0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12);height:65px;border-radius:8px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;transition-duration:100ms;margin-bottom:0.5em;width:100%}.contacts-container .contacts-item:hover{cursor:pointer}.contacts-container .contacts-item img{width:30px;height:30px;margin:0 1em}.contacts-container .back-home{text-transform:uppercase;padding:0.5em 1em;margin:4em 0 1em 0;border-radius:5px;opacity:0.8;transition-duration:300ms}.contacts-container .back-home:hover{opacity:1;background-color:#4f77a8;-webkit-transform:scale(1.1);transform:scale(1.1)}@media screen and (min-width: 600px){.contacts-container>div{width:470px}}

